body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a{
	color:#fff;
	text-underline: none;
}
li{
	list-style: none;
}
.ellipsis{
	overflow: hidden;
white-space: nowrap;
text-overflow: ellipsis;
}
@media only screen and (min-width:520px ) {
	.wrap{
		max-width: 520px;
	}
}
.wrap{
	   
    margin: 0 auto;
    position: relative;
}
.app{
	
	margin:0 auto;
	width:100%;
	/*min-height: calc(100vh);*/
	background:rgba(34,43,74,1);
	padding:0 .15rem;
	box-sizing: border-box;
	overflow: hidden;
}
.flex{
	display: -webkit-flex;
	display:flex;
	-webkit-justify-content: space-between;
	justify-content: space-between;
}
.flex-start{
	display: -webkit-flex;
	display:flex;
	-webkit-justify-content: flex-start;
	justify-content: flex-start;
}
.flex-around{
	display: -webkit-flex;
	display:flex;
	-webkit-justify-content: space-around;
	justify-content: space-around;
	
}
.img-box{
	overflow: hidden;
}
.img-box>img{
	width:100%;
	height:100%;
}
.rotateYXs{
	animation: rotateYXs 1s infinite;

}
@keyframes rotateYXs{
	0%{
		transform: scale(1,1);
		
		
	}
	50%{
		transform: scale(-1,1);
	}
	100%{
		transform: scale(1,1);
	}
}