.rank-wrap{
	width:100%;
	background:#0f1320;
	overflow: hidden;
	font-size:.25rem;
	max-width: 1000px;
	margin:0 auto;
}
.rank-wrap h3{
	padding:0.3rem .3rem 0;
	font-size:.26rem;
	color:#fff;
}
.rank-wrap .rank-th{
	margin:.4rem 0;
}
.rank-wrap .rank-th>div{
	width:25%;
	text-align: center;
}
.rank-wrap .rank-th .label{
	border-radius: .3rem;
	font-size:.24rem;
}
.rank-item{
	margin:.15rem auto;
}
.rank-item-wrap{
	margin-bottom: .2rem;
}
.rank-item-wrap::after{
	content: '';
	font-size: 0;
	display: block;
	width: 90%;
	height:.02rem;
	background:#2b2e38;
	margin:.2rem auto;
}

.rank-item>div{
	width:25%;
	text-align: center;
	font-size:.24rem;
	color:#999999;
}
.rank-item>div:last-child{
	color:#994444;
}
.rank-item img{
	width:.45rem;
	height:.52rem;
}


.menu-wrap{
	width:5.04rem;
	box-sizing: border-box;
