.eos-recarge-wrap{
	position: fixed;
	width:100%;
	height:100%;
	background:rgba(0,0,0,.7);
	top:0;
	left:0;
	z-index: 999;
	
}
.eos-recarge-wrap .eos-box{
	width: 75%;
    /* margin: 1rem auto; */
    background: #fff;
    border-radius: .3rem;
    padding: 0 .3rem .3rem;
    font-size: .6rem;
    position: absolute;
    top: calc(50% - 2.5rem);
    left: 14%;
}
@media only screen and (min-width:520px ) {
	.eos-recarge-wrap .eos-box{
		width:300px;
		left:calc(50% - 150px);
	}
}
.eos-recarge-wrap .eos-box .input-box{
	font-size:.6rem;
	font-weight: 600;
}
.eos-recarge-wrap .eos-box .input-box input{
	width: 80%;
    border: 0;
    border-bottom: .02rem solid #E1E1E1;
    font-size:.38rem;
}
.eos-recarge-wrap .eos-box .btn{
	    width: 40%;
    height: .63rem;
    border: 0;
    background: rgba(69,81,117,1);
    color: #fff;
    font-size: .3rem;
    line-height: .63rem;
    padding: 0;
}
input::-moz-placeholder{
	color:#999999;
	font-size:.38rem;
}
.eos-recarge-wrap .eos-box .eos-tip{
	color:red;
	font-size:.3rem;
	margin:.3rem 0
	
}
.eos-recarge-wrap .eos-box .canle{
	text-align:right;
}
.eos-recarge-wrap .eos-box .canle>img{
	width:.42rem;
	height:.42rem;
}
h3{
	margin-top:0;
}
