.app{
	padding-bottom: .3rem;
}
.nav-text{
	color: #fff;
    font-size: .35rem;
    padding-left: .3rem;
    text-shadow: 0px 4px 5px #D774FB;
}
.nav-wrap{
	margin:.4rem auto;
	overflow: hidden;
	padding:0 .15rem;
}
.nav-wrap .nav-rb{
	width:.83rem;
	height:.83rem;
}

.nav-wrap .nav-rb-text{
	width:1.66rem;
}

.nav-wrap .nav-menu{
	width:.36rem;
	height:.36rem;
	margin-top:.21rem;
}
.text-shadow{
	text-shadow:2px 2px 8px #8d1a94;
	font-size:.32rem;
	margin-top:.2rem;
	color:#fff;
	font-weight:600
}
.fun-1{
	width:3.6rem;
	height:1.6rem;
	border-radius: .3rem;
	background:#1f253a;
	font-size: .3rem;
	color:#8d1a94;
}
.fun-1 .img-box{
	width:1rem;
	height:1rem;
	margin:.32rem .2rem;
}
.fun-2 .img-box{
	width:3.69rem;
	height:1.67rem;
	position: relative;
}
.fun-2 span{
	position: absolute;
    left:1.7rem;
    top: .4rem;
    border: .02rem solid;
    color: #fff;
    font-size: .28rem;
    padding: .1rem;
}
